import {AUTH_ENUMS} from '../../../conf/enums'
import {enumToSelectOptions} from '../../../lib/filters'


export default {
    computed: {
        statusItems: () => enumToSelectOptions(AUTH_ENUMS)
    },

    data() {
        return {
            loading: true,
            keyword: '',
            status: '',
            list: {
                items: [],
                page: 1,
                totalPage: 0,
                pageSize: 0
            },
            dialogVisible: false,
            dialogPics: []
        }
    },

    components: {},

    mounted() {
        this.refresh()
    },

    methods: {
        onKeywordChange() {
            this.refresh(this.list.page)
        },

        refresh(page = 1) {
            this.loading = true
            this.$store.dispatch('authUserEnterpriseList', {
                data: {
                    page,
                    keyword: this.keyword,
                    status: this.status
                },
                success: data => {
                    this.list = data
                    this.loading = false
                },
                error: msg => {
                    this.alert(msg)
                    this.loading = false
                }
            })
        },

        pass(row) {
            this.confirm(`确认要通过[${row.name}]的个人实名认证吗？`, () => {
                this.$store.dispatch('authUserEnterprisePass', {
                    data: {
                        id: row.id,
                        pass: '1',
                        reason: ''
                    },
                    success: data => {
                        if (data) {
                            this.refresh(this.list.page)
                        }
                    }
                })
            })
        },

        reject(row) {
            this.prompt(`请输入拒绝[${row.name}]的理由`, msg => {
                this.$store.dispatch('authUserEnterprisePass', {
                    data: {
                        id: row.id,
                        pass: '0',
                        reason: msg
                    },
                    success: data => {
                        if (data) {
                            this.refresh(this.list.page)
                        }
                    }
                })
            })
        },

        view(row) {
            this.dialogVisible = true
            this.dialogPics = row.pics
        },

        viewClose() {
            this.dialogVisible = false
        }
    }
}